<template>
  <div class="find-container">
    <div class="input-contianer">
      <el-input
        v-if="serverLevel !== '2'"
        icon="search"
        placeholder="请输入产品名称/公司名称..."
        v-model="searchFor"
        :on-icon-click="handClickIcon"
        @keyup.enter.native="handClickIcon">
      </el-input>
    </div>
    <div class="loading loading-circle" v-if="isLoading"></div>
    <div v-if="!isLoading">
      <!--暂无数据-->
      <div class="no-data" v-show="display">
        <div class="no-info">
          <img src="../../assets/img/monitor/no-post.png" alt="">
          <p>暂无数据</p>
        </div>
      </div>
      <!--整体小卡片-->
      <ul>
        <li class="info-card" v-for="(item,index) in leadData">
          <!--上部显示-->
          <div class="person-info">
            <!--头像部分-->
            <div class="head-info">
              <!--type为1是公司-->
              <em v-if="item.type === 1" class="icon-font perCom">&#xe641;</em>
              <!--type为0是个人-->
              <em v-if="item.type === 0" class="icon-font perCom user">&#xe680;</em>
              <div class="img" v-if="item.type == 1" @click="jumpCompany(item.thirdAccountId, item.keywords)" >
                <div v-if="item.avatar == null || item.avatar == ''" class="companyDefault1">
                  <em  class="icon-company"></em>
                </div>
                <img v-else="" :src="item.avatar" alt="">
              </div>
              <div class="img" v-if="item.type == 0" @click="jumpStaff(item.thirdAccountId, item.keywords)">
                <img :src="item.avatar||'/static/img/default_avatar.jpg'" alt="">
              </div>
              <div class="name-info">
                <p class="name">
                  {{item.recommendName}}
                </p>
                <em class="icon-font plus-data" @click="addContact(item, index)">&#xe63e;</em>
                <p class="icon-data">
                  <span class="data">包含数据: </span>
                  <span v-for="icon in item.includeList">
                    <em class="icon-font phone" v-if="icon.value == '电话数据' && icon.included == 1">&#xe676;</em>
                    <em class="icon-font mail" v-if="icon.value == '邮箱数据' && icon.included == 1">&#xe614;</em>
                    <em class="icon-font user" v-if="icon.value == '员工数据' && icon.included == 1">&#xe680;</em>
                    <a :href="icon.url" target="_blank">
                      <em class="icon-font icon-facebook" v-if="icon.value == 'facebook数据' && icon.included == 1"></em></a>
                    <em class="icon-font lead" v-if="icon.value == '海关数据' && icon.included == 1">&#xe691;</em>
                    <a :href="icon.url" v-if="icon.value == 'twitter数据' && icon.included == 1" target="_blank">
                      <em class="icon-font tw" >&#xe621;</em>
                    </a>
                    <a :href="icon.url" v-if="icon.value == 'linkedin数据' && icon.included == 1" target="_blank">
                      <em class="icon-font icon-linkedin" ></em>
                    </a>
                  </span>
                </p>
                <em class="icon-font del-data" @click="removeContact(item.recommendForUserId, index, item.type)">&#xe63f;</em>
              </div>
            </div>


            <!--横线-->
            <div class="line"></div>
            <!--所属地、行业-->
            <div class="industry">
              <p v-if="item.type === 1" ><span>所在地 : </span>
                <span class="content">
                  {{item.address.location !== null && item.address.location !== '' ? item.address.location : '—'}}
                </span>
              </p>
              <p v-if="item.type === 1" ><span>行业 : </span>
                <span class="content">
                  {{item.industry !== null && item.industry !== '' ? item.industry : '—' }}
                </span>
              </p>
              <p v-if="item.type === 0" ><span>公司 : </span>
                <span class="content">
                  {{item.experienceList[0].thirdInfoCompanyName !== null && item.experienceList[0].thirdInfoCompanyName !== '' ? item.experienceList[0].thirdInfoCompanyName : '—'}}
                </span>
              </p>
              <p v-if="item.type === 0" ><span>职位 : </span>
                <span class="content">
                  {{item.experienceList[0].position !== null && item.experienceList[0].position !== '' ? item.experienceList[0].position : '—'}}
                </span>
              </p>
            </div>
            <!--内容描述-->
            <p class="describe">
              <span v-if="item.desc !== null && item.desc !== ''">{{item.desc}}</span>
              <span v-else="" class="no-desc">暂未添加任何简介</span>
            </p>
          </div>
          <!--关键词部分-->
          <div class="key-info">
            <p class="key" @click="hello">关键字 <span>#{{item.keywords}}#</span> 匹配项目:</p>
            <p class="key-num">
              <span v-for="keys in item.matchList">
                <span v-if="keys.isMatching == 1" class="keys">{{keys.value}}</span>
              </span>
            </p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import exploreAPI from '@/api/exploreAPI'
  import Util from '@/common/Util.js'
  export default{
    name: 'findCustomer',
    data () {
      return {
        searchFor: '',
        allList: [],
        leadData: [],
        leadComData: [],
        leadPerData: [],
        nowIndex: 6,
        nowComIndex: 0,
        nowPerIndex: 0,
        display: false,
        isLoading: true,
        userId: window.localStorage.userId,
        describe: '',
        serverLevel: '1'
      }
    },
    components: { exploreAPI },
    mounted () {
      this.serverLevel = window.localStorage.serverLevel
//      数据获取
//      this.infoList({userId: window.localStorage.userId})
      this.infoList()
    },
    methods: {
      infoList () {
        this.isLoading = true
        exploreAPI.getLeads().then(res => {
          this.isLoading = false
          if (res.data.code === 0) {
            if (res.data.data.length > 0) {
              for (let j = 0; j < res.data.data.length; j++) {
                for (let x = 0; x < res.data.data[j].includeList.length; x++) {
                  if (res.data.data[j].includeList[x].url !== null && res.data.data[j].includeList[x].url !== '') {
                    res.data.data[j].includeList[x].url = Util.checkUrl(res.data.data[j].includeList[x].url)
                  }
                }
              }
              this.display = false
//              push公司/员工
              for (let i = 0; i < res.data.data.length; i++) {
                if (res.data.data[i].type === 1) {
                  this.leadComData.push(res.data.data[i])
                  if (this.nowComIndex < 2) {
                    this.leadData.push(res.data.data[i])
                    this.nowComIndex++
                  }
                } else if (res.data.data[i].type === 0) {
                  this.leadPerData.push(res.data.data[i])
                  if (this.nowPerIndex < 6 - this.nowComIndex) {
                    this.leadData.push(res.data.data[i])
                    this.nowPerIndex++
                  }
                }
              }
//              for (let i = 0; i < res.data.data.length; i++) {
//                this.allList.push(res.data.data[i])
//              }
//              if (this.allList.length > 6) {
//                this.leadData = this.allList.slice(0, 6)
//              } else {
//                this.leadData = res.data.data
//              }
//              this.leadData.forEach((obj, i) => {
//                if (obj.desc.length > 160) {
//                  console.log(obj.desc.length)
//                  obj.desc = obj.desc.substr(0, 160) + '...'
//                }
//              })
            } else {
              this.display = true
            }
          }
        })
      },
//      搜索标题
      handClickIcon (event) {
        this.page = 1
        this.$store.commit('DEVELOP_SEARCH_PAGE', {page: this.page})
        if (this.searchFor === '') {
          alert('请输入搜索内容')
        } else {
          this.$router.push({
            path: '/searchResult',
            query: {searchText: this.searchFor}
          })
        }
      },
//      添加联系人
      addContact (item, index) {
        for (let i = 0; i < item.thirdInfoList.length; i++) {
          if (item.thirdInfoList[i].homepageUrl !== null && item.thirdInfoList[i].homepageUrl !== '') {
            item.thirdInfoList[i].homepageUrl = Util.checkUrl(item.thirdInfoList[i].homepageUrl)
          }
        }
        if (item.website !== null && item.website !== '') {
          item.website = Util.checkUrl(item.website)
        }
        if (item.type === 0) {
          const params = {
            type: item.type,
            userId: this.userId,
            name: item.recommendName,
            avatar: item.avatar,
            mail: item.mail,
            telephone: item.telephone,
            address: JSON.stringify(item.address),
            thirdAccountId: item.thirdAccountId,
            createSource: 'recommend_create',
            keywords: item.keywords,
            remark: item.desc,
            socialAccountType: 6,
            experienceList: JSON.stringify(item.experienceList),
            thirdInfoList: JSON.stringify(item.thirdInfoList)
          }
          exploreAPI.addContact(params).then(res => {
            if (res.data.code === 0) {
//              if (this.nowIndex < this.allList.length - 1) {
//                this.leadData.splice(index, 1, this.allList[this.nowIndex])
//                this.nowIndex++
//              } else {
//                this.leadData.splice(index, 1)
//              }
//              this.$message.success('添加联系人成功')
              if (this.nowPerIndex < this.leadPerData.length) {
                this.leadData.splice(index, 1, this.leadPerData[this.nowPerIndex])
                this.nowPerIndex ++
              } else if (this.nowComIndex < this.leadComData.length) {
                this.leadData.splice(index, 1, this.leadComData[this.nowComIndex])
                this.nowComIndex ++
              } else {
                this.leadData.splice(index, 1)
              }
              this.$message.success('添加联系人成功')
            } else {
              this.$message.fail('添加失败')
            }
          })
        } else if (item.type === 1) {
          const params = {
            type: item.type,
            userId: this.userId,
            name: item.recommendName,
            avatar: item.avatar,
            mail: item.mail,
            telephone: item.telephone,
            address: JSON.stringify(item.address),
            thirdAccountId: item.thirdAccountId,
            createSource: 'recommend_create',
            keywords: item.keywords,
            remark: item.desc,
            socialAccountType: 6,
            industry: item.industry,
            website: item.website,
            thirdInfoList: JSON.stringify(item.thirdInfoList),
            companySize: item.companySize
          }
          exploreAPI.addContact(params).then(res => {
            if (res.data.code === 0) {
//              if (this.nowIndex < this.allList.length - 1) {
//                this.leadData.splice(index, 1, this.allList[this.nowIndex])
//                this.nowIndex++
//              } else {
//                this.leadData.splice(index, 1)
//              }
//              this.$message.success('添加联系人成功')
              console.log('kkkk')
              console.log(this.nowComIndex)
              console.log(this.leadComData.length)
              if (this.nowComIndex < this.leadComData.length) {
                this.leadData.splice(index, 1, this.leadComData[this.nowComIndex])
                this.nowComIndex++
              } else if (this.nowPerIndex < this.leadPerData.length) {
                this.leadData.splice(index, 1, this.leadPerData[this.nowPerIndex])
                this.nowPerIndex++
              } else {
                this.leadData.splice(index, 1)
              }
              this.$message.success('添加公司成功')
            } else {
              this.$message.fail('添加失败')
            }
          })
        }
      },
//      移除联系人
      removeContact (id, index, type) {
        let params = {recommendForUserId: id, userId: window.localStorage.userId}
        if (type === 0) {
          exploreAPI.removeContact(params).then(res => {
            if (res.data.code === 0) {
              if (this.nowPerIndex < this.leadPerData.length) {
                this.leadData.splice(index, 1, this.leadPerData[this.nowPerIndex])
                this.nowPerIndex++
              } else if (this.nowComIndex < this.leadComData.length) {
                this.leadData.splice(index, 1, this.leadComData[this.nowComIndex])
                this.nowComIndex++
              } else {
                this.leadData.splice(index, 1)
              }
            }
          })
        }
        if (type === 1) {
          exploreAPI.removeContact(params).then(res => {
            if (res.data.code === 0) {
              console.log('deldeldel')
              console.log(this.nowComIndex)
              console.log(this.leadComData.length)
              if (this.nowComIndex < this.leadComData.length - 1) {
                this.leadData.splice(index, 1, this.leadComData[this.nowComIndex])
                this.nowComIndex ++
              } else if (this.nowPerIndex < this.leadPerData.length - 1) {
                this.leadData.splice(index, 1, this.leadPerData[this.nowPerIndex])
                this.nowPerIndex++
              } else {
                this.leadData.splice(index, 1)
              }
            }
          })
        }
//        exploreAPI.removeContact(params).then(res => {
//          if (res.data.code === 0) {
//            if (this.nowIndex < this.allList.length - 1) {
//              this.leadData.splice(index, 1, this.allList[this.nowIndex])
//              this.nowIndex++
//            } else {
//              this.leadData.splice(index, 1)
//            }
//            this.$message.success('移除联系人成功')
//          } else {
//            this.$message.error('移除联系人失败')
//          }
//        })
      },
//      跳转到公司详情
      jumpCompany (id, keywords) {
        console.log(id)
        this.$router.push(
          {
            path: '/companyDetail',
            query: {id: id, source: 'recommend', keywords: keywords}
          }
        )
      },
//      跳转到员工详情
      jumpStaff (id, keywords) {
        this.$router.push({
          path: '/staffDetail',
          query: {id: id, source: 'recommend', keywords: keywords}
        })
      },
      hello () {
        this.$store.dispatch('getList')
//        let list = this.$store.getters.leadList
        console.log(555)
        console.log(this.$store.getters.leadsList)
      }
    }
  }
</script>
<style lang="less" rel="stylesheet/less" >
  @import '../../assets/style/variable.less';
  .find-container{
    .input-contianer{
      width:1200px;
      .el-input{
        margin-bottom: 24px;
        .el-icon-search{
          left:0;
        }
        .el-input__inner{
          padding-left: 30px;
          height: 60px;
          &.focus{
            border: 1px solid #fff;
            box-shadow: 1px 0 1px 0 #fff;
          }
        }
      }
    }
    .no-data{
      .no-info{
        text-align: center;
        margin-top: 100px;
        p{
          margin-top: 10px;
        }
      }
    }
    .info-card{
      width:47%;
      height: 344px;
      margin-bottom: 30px;
      background: #fff;
      float: left;
      padding: 30px 10px 16px 10px;
      box-shadow: 0 0 8px 0 rgba(0,0,0,0.08);
      border-radius: 4px;
      &:hover{
        box-shadow: 0 0 14px 0 rgba(84,136,249,0.25);
      }
      .person-info{
        padding-left:20px;
        padding-right: 20px;
        .head-info{
          position: relative;
          margin-bottom: 15px;
          .perCom{
            width: 20px;
            height: 20px;
            line-height: 20px;
            position: absolute;
            left: 50px;
            bottom: 5px;
            text-align: center;
            font-size: 12px;
            z-index: 33;
            border-radius: 50%;
            background: #fff;
            color: cornflowerblue;
            box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.1);
          }
          .user{
            color: #00B8CC;
          }
          .img{
            width:70px;
            height: 70px;
            position: absolute;
            overflow: hidden;
            border-radius: 50%;
            z-index: 22;
            cursor: pointer;
            .companyDefault1{
              width: 70px;
              height: 70px;
              border-radius: 50%;
              background: #DEE2ED;
              em{
                font-size: 65px;
                color: #F2F5F8;
                position: absolute;
                top: 8px;
                left: 0px
              }
            }
            img{
              width:70px;
              height: 70px;
            }
          }
          .name-info{
            position: relative;
            padding: 10px 5px 10px 80px;
            .name{
              height: 25px;
              width:90%;
              line-height: 25px;
              font-size: 18px;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              color: #33475B;
              letter-spacing: 0;
              margin-bottom: 13px;
              position: relative;
            }
            .plus-data{
              font-size: 18px;
              position: absolute;
              right: 8px;
              top: 12px;
              color: #B0C1D4;
              cursor: pointer;
                &:hover{
                color: #5488F9;
              }
            }

            .icon-data{
              position: relative;
              width:90%;
              white-space: nowrap;
              /*overflow: hidden;*/
              text-overflow: ellipsis;
              .data{
                font-size: 12px;
                color: #7C98B6;
                letter-spacing: 0;
                padding-right: 5px;
              }
              .icon-font{
                margin-right: 10px;
              }
              .phone{
                color: #5488F9;
              }
              .mail{
                color:#00B8CC;
              }
              .user{
                color: #00A8FF;
              }
              .fb{
                color: #4D7CE7;
              }
              .tw{
                color: #00A8FF;
              }
              .linkedin{
                color: #279BD6;
              }
              .lead{
                color: #00A8FF;
              }
            }
            .del-data{
              font-size: 14px;
              position: absolute;
              right: 10px;
              top:40px;
              color: #B0C1D4;
              cursor: pointer;
                &:hover{
                color: #5488F9;
              }
            }
          }
        }
        .line{
          width:40px;
          height: 5px;
          background: #EAF0F6;
          border-radius: 8px;
          margin-bottom: 10px;
        }
        .industry{
          margin-bottom: 16px;
          p{
            height: 16px;
            line-height: 16px;
            font-size: 12px;
            color: #7C98B6;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            .content{
              padding-left: 8px;
            }
          }
        }
        .describe{
          /*position: relative;*/
          display: inline-block;
          width:99%;
          font-size: 13px;
          color: #4F6D95;
          overflow: hidden;
          height: 48px;
          line-height: 16px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -moz-box-orient: vertical;
          -ms-box-orient: vertical;
          -webkit-line-clamp: 3;
          -moz-line-clamp: 3;
          -ms-line-clamp: 3;
          .no-desc{
            color: #7C98B6;
          }
        }
        /*.describe::after{*/
          /*content: '...';*/
          /*position: absolute;*/
          /*bottom: 0;*/
          /*right: 0;*/
          /*padding:0 20px 1px 45px;*/
          /*background: url('../../assets/img/develop/ellipsis_bg.png') repeat-y;*/
        /*}*/
      }
      .key-info{
        margin-top: 20px;
        height: 102px;
        padding: 18px 20px 0 20px;
        background: #F5F8FA;
        border-top:2px solid #EAF0F6;
        .key{
          height: 22px;
          line-height: 22px;
          font-size: 16px;
          color: #33475B;
          span{
            font-size: 16px;
            color: #00B8CC;
          }
        }
        .key-num{
          margin-top: 16px;
          .keys{
            display: inline-block;
            width:70px;
            height: 28px;
            line-height: 28px;
            margin-right: 10px;
            font-size: 13px;
            border-radius: 4px;
            text-align: center;
            color: #00B8CC;
            background: #CCF0F4;
          }
        }
      }
    }
    li:nth-child(2n+1){
      margin-right: 2.4%;
    }
  }
</style>



